.vkuiPanel.vkuiPanel--regular .vkuiPanel__in {
    background: var(--background_content);
}

.Placeholder {
    margin: 74px 195px 64px 96px
}

.Main {
    margin: 48px 126px 37px 96px;
}

.Main .Colors__Container {
    width: fit-content;
}

.PlayContainer {
    top: 48px;
    right: 126px;
}

.Cards__Container > :last-child {
    margin-left: 23px;
}

.vkuiPanelHeader__fixed {
    position: relative;
    margin: 48px 96px 0 80px;
}

.vkuiPanelHeader--android::before {
    height: 0;
}

.Gallery, .Rating, .Likes {
    margin: 18px 96px 0 96px;
}

.Gallery p, .Rating p, .Likes p {
    margin: 0;
}

.Search {
    margin: 89px 190px;
}

.Search h1 {
    margin: 0;
}

.Game {
    margin: 38px 18px 0 48px;
}

.Comments_Container {
    height: 319px;
    width: 240px;
    margin-left: 35px;
}

.Comments {
    max-height: 263px;
    justify-content: end;
}

.Comment_Input {
    margin-top: 14px;
    width: 240px;
}

.Result {
    margin: 53px 171px 53px 170px;
}

.Result .Button--lvl-secondary {
    margin-top: 28px;
}

.Canvas_Container {
    position: relative;
    width: 319px;
    height: 319px;
    margin-top: 28px;

    background: #FFFFFF;
    box-shadow: 0px 0px 227px rgba(0, 0, 0, 0.15);
    border-radius: 28px;
    overflow: hidden;
}

.Likes__Container {
    width: 310px;
}

.PictureInfo {
    margin: 18px 48.5px 0 96px;
}

.PictureInfo > .Canvas_Container .Actions_Container .LikeButton {
    width: 148px;
}

.PictureInfo > .Author_Container {
    width: 319px;
}

.PictureInfo > .Actions_Container {
    position: absolute;
    top: 122px;
    right: 48.5px;
    width: 154.5px;
}